import React, { useState, useEffect } from "react"
import { Route, useHistory } from "react-router-dom"

import { formatFormData } from "../../forms/functions/form"
import * as denominacaoAPI from "../../services/functions/denominacao"
import ErrorBoundary from "../../components/ErrorBoundary"
import Datatable from "../../components/Datatable"
import ModalForm from "../../components/ModalForm"
import ConfirmModal from "../../components/UI/ConfirmModal"
import {
  initialValues,
  validationSchema,
  camposForm,
} from "../../forms/forms/denominacao"
// import { initialValues as initialValuesEdit, validationSchema as validationSchemaEdit, camposForm as camposFormEdit } from "./formDenominacaoEdit"
import { Edit, AddCircle, Delete, Sync } from "@material-ui/icons"
import estilos from "./estilos.module.css"

const Denominacoes = (props) => {
  const { appState } = props

  const [denominacoes, setDenominacoes] = useState([])
  const [denominacao, setDenominacao] = useState(null)
  const [linhaSelecionada, setLinhaSelecionada] = useState(null)

  const history = useHistory()
  const location = window.location.pathname.split("/")

  const colunasTabela = [
    {
      id: "nome",
      align: "left",
      numeric: false,
      disablePadding: false,
      label: "Nome",
    },
  ]

  const opcoesTabela = [
    {
      label: "Sincronizar Dados",
      click: () => fetchDenominacoesHandler(),
      icon: <Sync />,
    },
    {
      label: "Novo",
      click: () => {
        history.push("/denominacoes/novo")
      },
      icon: <AddCircle />,
    },
  ]

  const opcoesTabelaSelecionada = [
    {
      label: "Editar",
      click: () => {
        fetchDenominacaoHandler()
        history.push("/denominacoes/editar")
      },
      icon: <Edit />,
    },
    {
      label: "Excluir",
      click: () => {
        history.push("/denominacoes/excluir")
      },
      icon: <Delete />,
    },
  ]

  const successHandler = () => {
    setLinhaSelecionada(null)
    history.goBack()
    fetchDenominacoesHandler()
    appState.setSuccess()
  }

  const fetchDenominacoesHandler = async () => {
    const adms = await denominacaoAPI.fetchAll({ appState })

    setDenominacoes(adms)
  }

  const fetchDenominacaoHandler = async () => {
    const admin = await denominacaoAPI.fetchOne({
      id: linhaSelecionada,
      appState,
    })

    setDenominacao(admin)
  }

  const cadDenominacaoHandler = async (values) => {
    const res = await denominacaoAPI.create({
      denominacao: values,
      appState,
    })

    if (res) {
      successHandler()
    }
  }

  const editDenominacaoHandler = async (values) => {
    const res = await denominacaoAPI.update({
      denominacao: {
        ...values,
        id: linhaSelecionada,
      },
      appState,
    })

    if (res) {
      successHandler()
    }
  }

  const delDenominacaoHandler = async () => {
    const res = await denominacaoAPI.del({
      denominacao: {
        id: linhaSelecionada,
      },
      appState,
    })

    if (res) {
      successHandler()
    }
  }

  const onFormSubmit = async (values) => {
    let newValues = await formatFormData(values)

    if (location[2] === "novo") {
      cadDenominacaoHandler(newValues)
    } else if (location[2] === "editar") {
      editDenominacaoHandler(newValues)
    }
  }

  useEffect(() => {
    fetchDenominacoesHandler()
  }, [])

  return (
    <ErrorBoundary>
      <div className={estilos.Container}>
        <Datatable
          colunasKey={"id"}
          colunasTabela={colunasTabela}
          fetchedRows={denominacoes}
          linhasPorPaginaSelecionada={15}
          linhaSelecionada={linhaSelecionada}
          opcoesTabela={opcoesTabela}
          loading={appState.loading}
          opcoesTabelaSelecionada={opcoesTabelaSelecionada}
          setLinhaSelecionada={setLinhaSelecionada}
          tituloTabela={"Denominações"}
        />
      </div>

      <Route path="/denominacoes/excluir">
        <ConfirmModal
          action={"delete"}
          history={history}
          location={location}
          okButtonClick={delDenominacaoHandler}
        />
      </Route>

      <Route path="/denominacoes/novo">
        <ModalForm
          camposForm={camposForm({
            denominacoes: [],
            permissoes: [
              { label: "Administrador", value: "1" },
              { label: "Denominacao", value: "2" },
            ],
            loading: appState.loading,
          })}
          initialValues={initialValues}
          history={history}
          loading={appState.loading}
          location={location}
          onFormSubmit={onFormSubmit}
          tabLabel={[{ label: "Denominacao" }]}
          validationSchema={validationSchema}
        />
      </Route>

      <Route path="/denominacoes/editar">
        {linhaSelecionada ? (
          <ModalForm
            camposForm={camposForm({
              denominacoes: [],
              permissoes: [
                { label: "Administrador", value: "1" },
                { label: "Denominacao", value: "2" },
              ],
              loading: appState.loading,
            })}
            initialValues={denominacao ? denominacao : initialValues}
            history={history}
            loading={appState.loading}
            location={location}
            onFormSubmit={onFormSubmit}
            tabLabel={[{ label: "Denominacao" }]}
            validationSchema={validationSchema}
          />
        ) : null}
      </Route>
    </ErrorBoundary>
  )
}

export default Denominacoes
